<template>
    <div class="Bei">
        <!-- 头部 -->
            <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <div class="span1" slot="mid">{{this.$route.query.name}}</div>
            <p class="span2" slot="last">切换城市</p>
            </Header>

        <!-- 搜索框 -->
        <div class="city">
                <input type="text" placeholder="输入学校 商务楼 地址" v-model="inputval" @keyup.enter="add()" >
                <!-- <el-button type="primary" >提交</el-button> -->
                <input type="button" value="提交" @click="add()">
        </div>
        <p>搜索历史</p>
        <div class="yang">
            <ul>
                <li v-for='(item,index) in info' :key="index" @click="cun(item)">
                    <span>{{item.name}}</span>
                    <p>{{item.address}}</p>
                </li>
            </ul>
        </div>
        <div class="zhong" v-if="kong">
            <ul>
                <li v-for='(item,index) in list' :key="index" @click="chu(item)">
                    <span>{{item.name}}</span>
                    <p>{{item.address}}</p>
                </li>
            </ul>
            <div class="qing">
            <span @click="qing()">清空所有</span>
            </div>
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
export default {
    components:{
        Header
    },
   data() {
      return {
          inputval:"",
          info:[],
          list:[],
          kong:false
      }
   },
   created() {
       this.list=JSON.parse(localStorage.getItem("sb")) || []
   },
   methods: {
       add(){
           this.axios.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.$route.query.id}&keyword=${this.inputval}`).then(res=>{
               this.info=res.data
            //    console.log(res)
           })
       },
       cun(item){
           let i=this.list.findIndex(v=>{return v.name==item.name})
            if(i==-1){
                this.list.push(item)
                localStorage.setItem("sb",JSON.stringify(this.list))
            }else{
                return 
            }
            this.$router.push(`/home/takeout?name=${item.name}&geohash=${item.geohash}`)
       },
       qing(){
            this.info=[]
            this.list=JSON.parse(localStorage.getItem('sb'))
       },
       chu(item){
            this.$router.push(`/home/takeout?name=${item.name}&geohash=${item.geohash}`)
       }
   },
   mounted() {
       if(JSON.parse(localStorage.getItem("sb"))){
           var yao=JSON.parse(localStorage.getItem("sb"))
           this.list=yao
           if(this.list.length>0){
               this.kong=true
           }
       }else{
           return
       }
   },
   }
</script>

<style lang="scss">
*{
    margin: 0;
    padding: 0;
}
    body{
        background: #F5F6F5;
    }
    .city{
            width: 100%;height:1.6rem;background: white;
            border-bottom: 2px solid rgb(233, 232, 232);
            margin-top: 1rem;
        >input:nth-of-type(1){
            width: 6.5rem;height: 0.5rem;border: 1px solid gray;border-radius: 0.08rem;
            font-size: 0.25rem;padding-left: 0.1rem;display: flex;margin:0.3rem auto;
        }
        ul{
            width: 6.5rem;list-style: none;margin:0.3rem auto;
            li{
                font-size: 0.25rem;
            }
        }
        >input:nth-of-type(2){
            display: block;width: 6.5rem;border-radius: 0.08rem;
            font-size: 0.4rem;margin:0.2rem auto;border: none;
            background: #2F90E8;color: white;
        }
            
    }
    p{
        font-size: 0.23rem;padding-left: 0.2rem;
    }
    .yang{
        width: 100%;border-top: 2px solid rgb(233, 232, 232);
        background: white;
        ul{
            width: 100%;list-style: none;
            li{
                width: 100%;line-height: 0.5rem;
                border-bottom: 1px solid rgb(214, 211, 211);
                span{
                    font-size: 0.3rem;display: block;padding-left: 0.2rem;
                }
                p{
                    color: rgb(185, 184, 184);font-size: 0.25rem;margin-top: 0.15rem;
                }
            }
        }
    }
    .zhong{
        width: 100%;background: white;
        border-bottom: 2px solid rgb(233, 232, 232);
        ul{
            width: 100%;list-style: none;
            li{
                width: 100%;line-height: 0.5rem;
                border-bottom: 1px solid rgb(214, 211, 211);
                span{
                    font-size: 0.3rem;display: block;padding-left: 0.2rem;
                }
                p{
                    color: rgb(185, 184, 184);font-size: 0.25rem;margin-top: 0.15rem;
                }
            }
        }
    }
    .qing{
        width: 100%;background: white;
        height: 0.6rem;margin-top: 0.15rem;
        span{
            text-align: center;display: block;font-size: 0.3rem;
        }
    }
</style>


